import $ from 'jquery'
export default function(canvas, box) {
	let Btn = null;
	function removeBtn() {
		if(!Btn) return;
		Btn.remove();
		Btn = null;
		
	}
	function newBtn(x,y){
		var btnLeft = x - 10;
		var btnTop = y - 10;
		Btn = Btn || $('<div class="deleteBtn" style="top:' + btnTop + 'px;left:' + btnLeft + 'px;"></div>');
		box.append(Btn);
	}
	function addDeleteBtn(x, y) {
		if(!Btn){
			newBtn(x,y);
		}
		var btnLeft = x - 10;
		var btnTop = y - 10;
		Btn.css('top',btnTop)
		Btn.css('left',btnLeft)
	}
	canvas.on({
		'object:selected': function(e) {
			// console.log('object:selected',e)
			newBtn(e.target.oCoords.tr.x, e.target.oCoords.tr.y);
		},
		'selection:updated': function(e) {
			// addDeleteBtn(e.target.oCoords.tr.x, e.target.oCoords.tr.y);
			// removeBtn();
			// console.log('s:up',e)
			addDeleteBtn(e.target.oCoords.tr.x, e.target.oCoords.tr.y);
		},
		'mouse:down': function(e) {
			// console.log('mouse:down',e);
			if (!canvas.getActiveObject()) {
				removeBtn();
			}
		},
		'object:modified': function(e) {
			// console.log('object:modified',canvas.getActiveObject());
			// removeBtn()
			addDeleteBtn(e.target.oCoords.tr.x, e.target.oCoords.tr.y);
		},
		'object:scaling': function(e) {
			removeBtn()
			// addDeleteBtn(e.target.oCoords.tr.x, e.target.oCoords.tr.y)
		},
		'object:moving': function(e) {
			removeBtn()
			// console.log('moving');
			// addDeleteBtn(e.target.oCoords.tr.x, e.target.oCoords.tr.y)
		},
		'object:rotating': function(e) {
			removeBtn()
			// addDeleteBtn(e.target.oCoords.tr.x, e.target.oCoords.tr.y)
		}
	});
	box.on('touchstart click', '.deleteBtn', function() {
		var sObj = canvas.getActiveObject();
		if (sObj) {
			// sObj.remove();
			// if(sObj.type == 'activeSelection'){
				
			// }else{
				canvas.remove(sObj);
				removeBtn();
			// }
		}
	});
	$(document).on('click',function(e){
		removeBtn();
	})
};
