function getURL(kdy, url_) {
	//JS获取URL参数
	var url = typeof url_ == 'undefined' ? window.location.href : url_;
	var paraString = url
		.substring(url.indexOf(this.m) + 1, url.length)
		.replace(/\#.*/, '')
		.split('&');
	var paraObj = {};
	for (var i = 0,j; (j = paraString[i]); i++) {
		paraObj[j.substring(0, j.indexOf(this.v)).toLowerCase()] = j.substring(
			j.indexOf(this.v) + 1,
			j.length
		);
	}
	var returnValue = paraObj[kdy.toLowerCase()];
	if (typeof returnValue == 'undefined') {
		return '';
	} else {
		return decodeURIComponent(returnValue);
	}
}
function setURL() {
	//修改、添加,删除URL参数
	var set = function(key, val, url_) {
		var url = typeof url_ == 'undefined' ? window.location.href : url_,
			lurl = '',
			rurl = '';
		if (this.m == '?' && url.indexOf('#') != -1) {
			rurl = url.substring(url.indexOf('#'), url.length);
			url = url.replace(/\#.*$/, '');
		}
		if (url.indexOf(this.m) <= -1) {
			url += this.m;
		}
		lurl = url.substring(0, url.indexOf(this.m));
		var paraString = url.substring(url.indexOf(this.m) + 1, url.length),
			key = key || '',
			val = typeof val === 'undefined' || val === null ? '' : val;
		if (key == '') {
			return url.replace(/\?*\s*$/, '');
		}
		var reg = new RegExp('\\b' + key + '=[^&]*', '');
		if (reg.test(paraString) && val !== '') {
			//值不为空，则替换
			paraString = paraString.replace(reg, key + this.v + val);
		} else if (reg.test(paraString) && val == '') {
			//值为空，则删除
			paraString = paraString.replace(reg, '');
		} else if (val !== '') {
			//没有则添加
			paraString += '&' + key + this.v + val;
		}
		paraString = paraString.replace(/(^&*|&*$|&*(?=&))/g, '');
		return lurl + (paraString == '' && this.m !== '#' ? '' : this.m + paraString) + (rurl ? rurl : '');
	};
	if (typeof arguments[0] == 'string') {
		return set.apply(this, [arguments[0], arguments[1], arguments[2]]);
	} else if (typeof arguments[0] == 'object') {
		var arg = arguments[0],
			url = typeof arguments[1] == 'undefined' ? window.location.href : arguments[1];
		for (aaa in arg) {
			url = set.apply(this, [aaa, arg[aaa], url]);
		}
		return url;
	} else {
		return location.href;
	}
}
var data = {
	data_hash: { m: '#', v: '=' },
	data_search: { m: '?', v: '=' }
};

export default {
	getSearch: function() {
		return getURL.apply(data.data_search, arguments);
	},
	setSearch: function() {
		return setURL.apply(data.data_search, arguments);
	},
	getHash: function() {
		return getURL.apply(data.data_hash, arguments);
	},
	setHash: function() {
		return setURL.apply(data.data_hash, arguments);
	},
	getmy: function(a, b) {
		var o = { m: a, v: b };
		o.getURL = getURL;
		return o.getURL;
	},
	setmy: function(a, b) {
		var o = { m: a, v: b };
		o.setURL = setURL;
		return o.setURL;
	}
};
