$(function() {
	homePageInit();
	Promise.all([CanvasInit(), getRank()]).then(([canvas, rank]) => {
		// console.log('init');
		//添加删除按钮
		$('#rank').text(rank);
		let selectImgBox = selectImgInit(canvas);
		bgColorsInit(canvas);
		delBtn(canvas, $('#canvas'));
		dragulaInit(canvas, selectImgBox);
		// shareInit(canvas, rank);
		commitButInit(canvas, rank);
	});
	// $(document).on('click',function(){
	// 	selectImgBox.trigger('hide');
	// })
});

function commitButInit(canvas, rank) {
	function pass(e) {
		return $(e).hasClass('active');
	}
	let outImgBox = $('#outImgBox');
	let canvasBox = $('#canvas');
	let bgbox = $('#bgbox');
	let imgbox = $('#imgbox');
	let out = $('.btns .bt_out').addClass('active');
	let back = $('.btns .bt_back');
	let save = $('.btns .bt_save');
	let share = $('.btns .bt_share');

	function btnStatus(isOut) {
		let c = 'active';
		let h = 'hide';
		if (isOut) {
			out.removeClass(c);
			outImgBox.removeClass(h);
			back.addClass(c);
			save.addClass(c);
			share.addClass(c);
			canvasBox.addClass(h);
			bgbox.addClass(h);
			imgbox.addClass(h);
		} else {
			out.addClass(c);
			outImgBox.addClass(h);
			back.removeClass(c);
			save.removeClass(c);
			share.removeClass(c);
			canvasBox.removeClass(h);
			bgbox.removeClass(h);
			imgbox.removeClass(h);
		}
	}
	out.click(function() {
		if (!pass(this)) return;
		out.removeClass('active');
		getImageForBase64(canvas, rank).then(DataURL => {
			outImgBox.find('img').attr('src', DataURL);
			save.find('img').attr('src', DataURL);
			btnStatus(true);
		});
	});
	back.click(function() {
		if (!pass(this)) return;
		btnStatus(false);
	});
	share.click(function() {
		if (!pass(this)) return;
		alert('请点击右上角图标 "分享到朋友圈"');
	});
}

function homePageInit() {
	const home = $('#home');
	home.find('img')
		.on('load', function() {
			setTimeout(() => {
				home.addClass('hide');
			}, 3000);
		})
		.each(function() {
			if (this.complete) $(this).trigger('load');
		});
}

function getRank() {
	if (isDev) {
		return '00001';
	} else {
		return new Promise(function(reslove, reject) {
			let url = window.location.href.split('#')[0];
			$.post(apis.getTicket, { url }).then(data => {
				weixingInit(url, data);
				reslove(data.rank);
			});
		});
	}
}

function CanvasInit() {
	// var g_width = document.documentElement.clientWidth;
	var g_width = document.getElementById('canvas').clientWidth;
	var r_height = 1;
	var canvas = new fabric.Canvas('c', {
		// enablePointerEvents: true,
		selection: false,
		width: g_width,
		height: g_width * r_height
	});
	canvas.backgroundColor = 'rgba(255,255,255,1)';
	canvas.renderAll();
	window.canvas = canvas;
	$(document).on('click', function(e) {
		// console.log('d c');
		// canvas.setActiveObject({});
		canvas._activeObject = null;
		canvas.renderAll();
	});

	return canvas;
	// canvas.enablePointerEvents = true;
	// return ImageInit(canvas).then(() => canvas);
}

// 背景色选择器 HTML 初始化
function bgColorsInit(canvas) {
	let bgbox = $('#bgbox');
	var html = '';
	bg_colors.map(v => {
		html += `<li style="background:${v}" data-color="${v}" ></li>`;
	});
	bgbox
		.find('ul')
		.html(html)
		.on('click', 'li', function() {
			canvas.setBackgroundColor($(this).data('color'));
			canvas.renderAll();
		});
}

// 拖拽元素及绘制
function dragulaInit(canvas, selectImgBox) {
	let addImg = getAddImg(canvas);
	let drake = dragula({
		isContainer: function(el) {
			if (el.id === 'canvas') return true;
			if (el.id === 'selectImgBox') return true;
			return false; // only ele	ments in drake.containers will be taken into account
		},
		moves: function(el, source, handle, sibling) {
			// console.log(el, source, handle, sibling);
			return el.classList.contains('item'); //只有class为item的元素可拖动
		},
		accepts: function(el, target, source, sibling) {
			// 拖到别一容器时接受?
			return true; // elements can be dropped in any of the `containers` by default
		},
		invalid: function(el, handle) {
			return false; // don't prevent any drags from initiating by default
		},
		direction: 'vertical', // Y axis is considered when determining where an element would be dropped
		copy: true, // elements are moved by default, not copied
		copySortSource: false, // elements in copy-source containers can be reordered
		revertOnSpill: false, // spilling will put the element back where it was dragged from, if this is true
		removeOnSpill: false, // spilling will `.remove` the element, if this is true
		mirrorContainer: document.body, // set the element that gets mirror elements appended
		ignoreInputTextSelection: true // allows users to select input text, see details below
	});

	// $('#imgbox .p1').flickity();
	drake.on('drag', function(el, source) {
		setTimeout(function() {
			selectImgBox.trigger('hide');
		}, 100);
		// console.log('drag');
		// $('.gu-mirror').addClass('a');
	});
	drake.on('drop', function(el, target, source, sibling) {
		var wz = $('.gu-mirror img')[0].getBoundingClientRect();
		var cwz = $('#c')[0].getBoundingClientRect();
		var imgUrl = $('.gu-mirror img').attr('src');
		var isLian = $('.gu-mirror').hasClass('lian');
		// console.log(el);
		if (target) {
			// console.log(el.src,jx)
			// console.log(wz, cwz);
			// console.log(el.src, { left: wz.left - cwz.left, top: wz.top - cwz.top });
			addImg(
				imgUrl,
				{
					left: wz.left - cwz.left,
					top: wz.top - cwz.top,
					width: wz.width,
					height: wz.height
				},
				isLian
			);
		}
	});
}

