function weixingInit(url, data) {
	let { timestamp, noncestr: nonceStr, signature } = data;
	let { appId, debug, icon: imgUrl } = window.__config;
	let title = '自然梦想家 2019';
	let desc = '自然梦想家 2019';
	wx.config({
		debug,
		appId,
		timestamp,
		nonceStr,
		signature,
		jsApiList: ['onMenuShareTimeline']
	});
	wx.ready(function() {
		wx.checkJsApi({
			jsApiList: ['onMenuShareTimeline']
		});
		wx.onMenuShareTimeline({
			title: title,
			desc: desc,
			link: url,
			imgUrl: imgUrl,
			success: function() {
				debug && alert('分享成功');
				// 用户确认分享后执行的回调函数
			},
			cancel: function() {
				debug && alert('取消分享');
				// 用户取消分享后执行的回调函数
			}
		});
	});
}

function getImageForBase64(canvas, rank) {
	let { width, height } = canvas;
	return Promise.all([getBannerObj(canvas), getTextObj(canvas, rank)]).then(function([B, T]) {
		return new Promise(function(reslove, reject) {
			let Bh = B.height * B.scaleY;
			let Th = T.height;
			let C = new fabric.StaticCanvas();
			C.set({
				width: width,
				height: height + Bh // + Th
			});
			C.loadFromDatalessJSON(canvas.toDatalessJSON(), function(err) {
				if (!err) {
					let BG = new fabric.Rect({
						left: 0,
						top: height,
						fill: '#FFF',
						width: width,
						height: Bh + Th
					});
					B.set({
						left: 0,
						top: height
					});
					T.set({
						left: -1,
						top: height + Bh / 3
					});
					C.add(BG, B, T);
					// console.log(
					// 	C.toDataURL({
					// 		format: 'png',
					// 		enableRetinaScaling: true
					// 	})
					// );
					let DataURL = C.toDataURL({ format: 'png', enableRetinaScaling: true });
					// if (isDev) {
					// 	$('#outimg').attr('src', DataURL);
					// }
					reslove(DataURL);
				} else {
					reject(err);
				}
			});
		});
	});
}

function getBannerObj(canvas) {
	return new Promise(function(reslove, reject) {
		fabric.Image.fromURL(require('./img/outImage_button.png'), function(img) {
			var s = canvas.width / img.width;
			img.set({
				scaleX: s,
				scaleY: s
			});
			return reslove(img);
		});
	});
}

function getTextObj(canvas, rank) {
	var textbox = new fabric.Rect({
		width: canvas.width,
		height: canvas.width * 0.18,
		originX: 'center',
		originY: 'center',
		fill: 'rgba(255,255,255,0)'
	});
	var text = new fabric.Text(`我是第 ${rank} 位 #自然梦想家#`, {
		fontSize: canvas.width * 0.062,
		fontFamily:
			"-apple-system,'SF UI Text',Arial,'PingFang SC','Hiragino Sans GB','Microsoft YaHei','WenQuanYi Micro Hei',sans-serif",
		fill: '#333',
		originX: 'center',
		originY: 'center'
	});
	var group = new fabric.Group([textbox, text]);
	return Promise.resolve(group);
	// return Promise.resolve(text);
}

// 元素选择弹层
function selectImgInit(canvas) {
	let selectImgBox = $('#selectImgBox');
	let hide = function() {
		selectImgBox.trigger('hide');
	};
	let imgCache = {};
	selectImgBox
		.on('show', function() {
			// console.log('show');
			// $(this).removeAttr('style');
			$(this).removeClass('hide');
			$(document).on('click', hide);
		})
		.on('hide', function() {
			// console.log('hide');
			$(this).addClass('hide');
			// $(this).attr('style', 'display:none');
			$(document).off('click', hide);
		})
		.on('click', function() {
			return false;
		});
	$('#imgbox').on('click touchstart', '[data-type]', function() {
		let type = $(this).data('type');
		let imgs = imgCache[type];
		if (!imgs) {
			let images = elements[type];
			let html = '';
			if (images) {
				images.forEach(url => {
					// html += `<div class="item ${type}"><img src="${url}" /></div>`;
					html += `<div class="item ${type}"><img class="loading" src="${url}" /></div>`;
				});
			}
			imgs = imgCache[type] = $(html);
			imgs.find('img').on('load', function() {
				let dom = $(this);
				dom.removeClass('loading');
				// setTimeout(function() {
				// }, 200);
			});
		}
		selectImgBox
			.html('')
			.append(imgs)
			.trigger('show')
			.find('img');
		return false;
	});
	canvas.on('mouse:down', function() {
		selectImgBox.trigger('hide');
	});
	// canvas.on('tap', function() {
	// 	selectImgBox.trigger('hide');
	// });
	return selectImgBox;
}

// 初始化 图片绘制函数
function getAddImg(canvas) {
	return function addImg(src, position, lian) {
		// console.log(src, position);
		fabric.Image.fromURL(src, function(img) {
			// console.log(img);
			img
				// .scale(position.width/img.width,position.height/img.height)
				.set({
					left: position.left,
					top: position.top,
					scaleX: position.width / img.width,
					scaleY: position.height / img.height
					// width: v.size[0],
					// height: v.size[1]
					// lockScalingX: true,
				});
			img.setControlsVisibility(HideControls);
			img.set(CornersOptions);
			img.on('after:scaling', function() {
				// console.log(arguments);
			});
			canvas.add(img);
			// 脸在最下面
			if (lian) {
				canvas.moveTo(img, 0);
			} else {
				canvas.setActiveObject(img);
			}
			canvas.renderAll();
		});
	};
}

